
.wedo-top.apps {
  background-image: url("../img/desarrollo-apps-bogota.jpg"); }
.wedo-top.marketing {
  background-image: url("../img/marketing-digital.jpg"); }
.wedo-top.web {
  background-image: url("../img/diseno-paginas-web-bogota.jpg"); }
.wedo-top {
  background: #538BF4;
  background-size: cover;
  height: rem(224); }
.wedo-top {
  background: #538BF4;
  background-size: cover;
  height: rem(224);
  h1 {
    font-size: rem(36);
    color: white;
    @include respond-to(medium) {
      font-size: rem(40); } } }
.wedo-title {
  padding: 1rem 1rem .2rem;
  background: #f5f8ff;
  @include respond-to(medium) {
    padding: 3rem 8rem 2.5rem; }
  h2 {
    font-size: rem(20);
    font-weight: 500;
    line-height: 1.3;
    margin-bottom: 1.5rem;
    @include respond-to(medium) {
      font-size: rem(30);
      margin-bottom: 3rem; } }
  p {
    font-size: 1em;
    font-weight: 100;
    line-height: 1.61; } }

.wedo-content {
  padding: rem(50) 0 3rem;
  background: url(../img/1.svg) bottom no-repeat;
  background-size: contain;
  position: relative;
  @include respond-to(medium) {
    padding: rem(50) 0 rem(128); }
  img {
    max-width: 100%; }
  article {
    margin: 0 auto 3.75rem;
    padding-bottom: rem(26);
    box-shadow: 0px 2px 4px rgba(45, 81, 148, 0.12); }

  h3 {
    font-weight: 300;
    margin-bottom: rem(10);
    line-height: 1.1em;
    color: #212121;
    padding: 0 rem(26);
    text-align: left;
    font-weight: 500; }
  p {
    padding: 0 rem(26);
    font-size: 1rem;
    font-weight: 100;
    line-height: 1.61; } }


.particles-js-canvas-el {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1; }

.wedo-apps , .wedo-marketing {
  article {
    box-shadow: none !important;
    padding-bottom: 0;
    @include respond-to(medium) {
      padding-bottom: 1.625rem; } }
  h2 {
    font-size: 1.75rem;
    text-align: left; }
  p {
    padding: 0; } }

.wedo-marketing {
  figure {
    order: -1;
    margin: auto;
    @include respond-to(medium) {
      order: 0; } }
  article:first-child {
    padding-bottom: 0 !important;
    margin: 0 auto 0; } }
.elearning , .ThreeD {
  article {
    margin: 0 auto 0;
    padding-bottom: 0 !important; } }
