@import "config";
@import "theme/header";
@import "theme/home";
@import "theme/contact";
@import "theme/footer";
@import "theme/wedo";
@import "theme/portfolio";
@import "theme/blogs";

.Culture {
  padding: 0 0   rem(100);
  background: url(../img/1.svg) bottom no-repeat #F6F7FB;
  background-size: contain;

  @include respond-to(medium) {
    padding: 0  0  rem(180); }
  section.first > div {
    margin: rem(10) 0 rem(30) 0; }

  article {
    background: white; }
  h2 {
    margin: 0 0 0 rem(20);
    font-size: rem(24);
    color: #36425B;
    font-weight: 400; }
  b {
    font-weight: 800;
    text-transform: uppercase; }
  p {
    line-height: rem(30);
    margin: 1.375rem 0 0;
    font-size: 1.125rem;
    font-weight: 100;
    &.label {
      margin: 0 0 rem(24);
      color: #9394A7;
      font-weight: 600;
      font-size: rem(12);
      line-height: 1.5;
      text-transform: uppercase;
      letter-spacing: rem(1);
      font-family: 'Raleway', sans-serif; } }
  .content {
    padding: rem(48)  rem(30) 0; }
  article {
    border-radius: rem(8);
    box-shadow: 0 rem(7) rem(8) 0 rgba(102,102,102,0.03);
    margin-bottom: rem(32);
    max-width: rem(320);
    min-width: rem(320);
    &:hover .Culture-member {
      background-position: 0 0; }
    h4 {
      font-size: rem(20);
      color: #36425B;
      font-weight: 400;
      margin-bottom: 0; }
    h5 {
      font-size: rem(12);
      font-weight: 300;
      margin: 0;
      a {
        color: #A9A9A9; } }
    p {
      font-size: rem(12);
      color: #A9A9A9;
      text-align: center; }
    span {
      padding: rem(12) rem(17) rem(10);
      border: rem(1) solid #BCBCBC;
      border-radius: rem(24); } } }

.Culture-memberSocial {
  border-top: rem(1) #dfdfdf solid;
  margin: rem(24) 0 0;
  padding: 1rem 0 rem(12);

  a {
    margin: 0 10px;
    display: block;
    &:hover g {
      fill: #4482FF; } } }

.Culture-header {
  width: 100%;
  height: rem(380);
  background: url(../img/bta.jpg) no-repeat;
  background-size: cover;
  overflow: hidden;
  position: relative;

  video {
    position: absolute;
    top: 18%;
    left: 50%;
    transform: translate(-50%, -15%);
    width: 1913px;
    height: 1076.06px; } }

.Culture-member {
  height: rem(224);
  border-radius: rem(8) rem(8) 0 0;
  background-position: 0 rem(224);
  background-size: 100%; }
.pattern {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  background: rgba(0,0,0,0.2) url(../img/pattern.png) repeat 0 0; }

.Rectangle {
  background: #4482FF;
  box-shadow: 0 8px 13px 0 rgba(68,130,255,0.37);
  border-radius: 2.19px;
  width: rem(35);
  height: rem(35); }

.Team {
  article:last-child {
    border: 1px dashed #BABECB;
    background: none;
    height: rem(410);
    position: relative; }
  em {
    display: block;
    max-width: rem(200);
    margin: rem(10) auto;
    color: #BBC1CD;
    font-weight: 100; }
  a.form {
    position: absolute;
    display: block;
    height: rem(50);
    width: rem(50);
    border-radius: 50%;
    background: #4482FF;
    box-shadow: 0 rem(8) rem(13) 0 rgba(68,130,255,0.37);
    bottom: rem(-16);
    right: rem(-16);
    &:after, &:before {
      content: '';
      position: absolute;
      height: rem(12);
      width: rem(3);
      top: calc(50% - 6px);
      left: calc(50% - 1px);
      background: white; }
    &:before {
      transform: rotate(90deg); } } }
.error404 {
  height: calc(100vh - 10rem);
  margin-top: rem(58);
  background: red;
  background: url(../img/montain.svg) bottom center no-repeat #5E89E0;
  h1 {
    font-size: 5rem;
    color: white;
    text-align: center;
    margin: 0;
    text-shadow: -2px 3px 2px rgba(0, 0, 0,.2); }
  h2 {
    font-size: rem(20);
    color: white;
    font-weight: 100;
    margin: rem(8);
    font-family: Raleway, sans-serif; }

  div {
    position: relative;
    top: rem(-40); } }
.error404-polo .Polo-footer {
  height: 4.75rem; }


